.form {
    width: 340px;  
    background: #e6e6e6;
    border-radius: 8px;
    box-shadow: 0 0 40px -10px #000;
    margin: 20px auto;
    padding: 10px 30px 80px 30px ;
    max-width: calc(100vw - 40px);
    
  }
  .titel {    
    padding-bottom: 10px;
    
    color: #78788c;
    border-bottom: 3px solid #78788c;
    text-align: center;
  }
  .input {
    width: 100%;
    padding: 10px;
  
    background: none;
    outline: none;
    resize: none;
    border: 0;
    transition: all 0.3s;
    border-bottom: 2px solid #bebed2;
  }
  .input:focus {
    border-bottom: 2px solid #78788c;
  }
  .btn {
    float: right;
    padding: 8px 12px;
    margin: 18px 0 0;
    border: 2px solid #78788c;
    background: #4CAF50;
    border-radius: 15px;
    box-shadow: 0 9px #999;
    color: #5a5a6e;
    cursor: pointer;
    transition: all 0.3s;
  }
  .btn:hover {
    background: #78788c;
    color: #fff;
  }
  .btn:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);}